#to-do-form input {
  background-image: url(data:image/png;base64,);
  transition: 0.4 ease-out;
  border: 0;
  width: 193px;
  height: 49px;
  padding: 0 20px;
  margin: 110px 20px 10px 100px;
  font-size: 18px;
  color: rgb(255, 255, 255);
}

#to-do-form input::-webkit-input-placeholder {
  color: rgb(255, 255, 255);
  font-family: "Permanent Marker", cursive;
}

#to-do-form input:-ms-input-placeholder {
  color: rgb(255, 255, 255);
  font-family: "Permanent Marker", cursive;
}

#to-do-form input::-ms-input-placeholder {
  color: rgb(255, 255, 255);
  font-family: "Permanent Marker", cursive;
}

#to-do-form input::placeholder {
  color: rgb(255, 255, 255);
  font-family: "Permanent Marker", cursive;
}

#to-do-form input:focus,
#to-do-form button {
  outline: none;
}

#to-do-form button {
  background-color: transparent;
  margin-top: -10px;
  height: 41px;
  width: 119px;
  font-family: "Permanent Marker", cursive;
  border: 0;
  background-image: url(data:image/png;base64,);
  background-size: cover;
  cursor: pointer;
  font-weight: bolder;
  font-size: 19px;
  color: rgb(0, 0, 0);
}

.list {
  background-image: url(data:image/png;base64,);
  color: rgb(0, 0, 0);
  height: 41px;
  width: 312px;
  margin: 19px 20px -10px 100px;
  border-radius: 10px;
}

.list p {
  padding: 10px 0px 0px 20px;
  font-family: "Permanent Marker", cursive;
  position: relative;
  transition: ease-in-out;
}

.list p span {
  position: absolute;
  bottom: 0px;
  right: 10px;
}
.faicons {
  margin: 0 5px;
  font-size: 15px;
  cursor: pointer;
}
.list p input {
  background-color: transparent;
  border: 0;
  color: rgb(0, 0, 0);
  width: 300px;
  font-size: 14px;
}
.list p input:focus {
  outline: none;
}

